<script>
import ColorSelector from "../../../components/V3ColorPicker/V3ColorPicker.vue"
export default {
  name: "PolylinesConfig",
  components: {
    ColorSelector
  },
  props: {
    model: {
      type: [Object],
      required: true
    }
  },
  data () {
    return {
      labelSpan: 6,
      wrapperSpan: 18
    }
  },
  methods: {
    add () {
      const path = [
        this.model.map.center,
        [ this.model.map.center[0]+0.005, this.model.map.center[1] ]
      ]
      const item = {
        visible: true,
        draggable: false,
        path: path,
        editable: false,
        zIndex: 10,
        strokeColor: '#006600',
        strokeOpacity: 0.9,
        strokeWeight: 3,
        borderWeight: 1,
        isOutline: false,
        outlineColor: '#000000',
        strokeStyle: 'solid',
        strokeDasharray: [0, 0, 0],
        lineJoin: 'miter',
        lineCap: 'butt',
        geodesic: false,
        showDir: false
      }
      this.model.polylines.push(item)
    },
    remove (index) {
      this.model.polylines.splice(index, 1)
    },
    addPoint (index) {
      const lastIndex = this.model.polylines[index].path.length - 1
      const point = [
        this.model.polylines[index].path[lastIndex][0] - 0.005,
        this.model.polylines[index].path[lastIndex][1] - 0.005
      ]
      this.model.polylines[index].path.push(point)
    },
    removePoint (index, idx) {
      this.model.polylines[index].path.splice(idx, 1)
    }
  },
}
</script>

